// ConfigContext.js
import React, {createContext, useContext} from 'react';
import {hubspotData} from './types';

const ConfigContext = createContext(null);

// eslint-disable-next-line react/prop-types
export const ConfigProvider = ({children, hubspotData}) => {
    const config = {
        host: process.env.REACT_APP_JCONTENT_HOST,
        workspace: process.env.REACT_APP_JCONTENT_WORKSPACE,
        files: process.env.REACT_APP_JCONTENT_FILES_ENDPOINT,
        gqlServerUrl: process.env.REACT_APP_JCONTENT_GQL_ENDPOINT,
        contextServerUrl: process.env.REACT_APP_JCUSTOMER_ENDPOINT,
        hubspotBaseUrl: process.env.REACT_APP_hubspot_BASE_URL,
        hubspotApiToken: process.env.REACT_APP_hubspot_API_TOKEN,
        hubspotTitle: hubspotData.content.title
    };

    return (
        <ConfigContext.Provider value={config}>
            {children}
        </ConfigContext.Provider>
    );
};

ConfigProvider.propTypes = {
    hubspotData
};

export const useConfig = () => useContext(ConfigContext);
