const hubspotService = {
    createLead: async (payload, baseUrl) => {
        try {
            const formattedPayload = {properties: payload};

            const response = await fetch(baseUrl, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(formattedPayload)
            });
            console.log('Full Response:', response);

            if (!response.ok) {
                const errorText = await response.text();
                console.error('Error response:', errorText);
                throw new Error('Failed to create lead: ' + errorText);
            }

            /* // Safely handle empty responses
            const text = await response.text();
            if (!text) {
                console.warn('Response is empty');
                return {message: 'No content returned from the server'};
            } */

            return response;
        } catch (error) {
            console.error('Error calling HubSpot Action:', error);
            throw error;
        }
    },

    getLeadById: async (leadId, baseUrl) => {
        try {
            const response = await fetch(`${baseUrl}?leadId=${leadId}`, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json'
                }
            });

            return response;
        } catch (error) {
            console.error('Error fetching lead:', error);
            throw error;
        }
    }
};

export default hubspotService;
