import React from 'react';
import {createRoot} from 'react-dom/client';
import {ApolloProvider} from '@apollo/client';

import App from './components/App';
import {PrimeReactProvider} from 'primereact/api';
// PrimeReact theme and other global styles
import 'primereact/resources/themes/lara-light-indigo/theme.css';
import 'primereact/resources/primereact.min.css';
import 'primeicons/primeicons.css';
import 'primeflex/primeflex.css';
import './i18n/i18n';
import {ConfigProvider} from './ConfigContext';
import {getClient, GetHubspot} from './webappGraphql';
import {contextValidator} from './douane';
import i18n from 'i18next';
import {initReactI18next} from 'react-i18next';
import {appLanguageBundle} from './i18n/i18n';

import './index.css';
import './flags.css';
import {ErrorHandler} from './components/Error';
import * as serviceWorker from './misc/serviceWorker';
import {formatHubspotJcrProps} from './components/Hubspot/HubspotModel';
import {JahiaCtxProvider, AppCtxProvider, CxsCtxProvider} from './contexts';
import {syncTracker} from './misc';

async function getHubspotData({client, workspace, locale, hubspotId}) {
    const {data} = await client.query({
        query: GetHubspot,
        variables: {
            workspace,
            language: locale,
            id: hubspotId
        }
    });
    return formatHubspotJcrProps(data.response.hubspot);
}

const render = async (target, context) => {
    const rootElement = document.getElementById(target);
    if (!rootElement) {
        console.error(`Error: Target element with ID "${target}" not found in the DOM.`);
        return;
    }

    const root = createRoot(rootElement);

    try {
        context = contextValidator(context);
        const {
            host,
            workspace,
            isEdit,
            locale,
            gqlServerUrl,
            contextServerUrl,
            appContext,
            cndTypes,
            scope,
            previewCm,
            previewTarget,
            hubspotId

        } = context;

        await i18n
            .use(initReactI18next)
            .init({
                resources: appLanguageBundle,
                lng: locale,
                fallbackLng: 'en',
                interpolation: {escapeValue: false}
            });
        const isPreview = workspace !== 'LIVE';

        const client = getClient(gqlServerUrl);
        const hubspotData = await getHubspotData({client, workspace, locale, hubspotId});
        if (workspace === 'LIVE' && !window.wem) {
            if (!window.digitalData) {
                window.digitalData = {
                    _webapp: true,
                    scope,
                    site: {
                        siteInfo: {
                            siteID: scope
                        }
                    },
                    page: {
                        pageInfo: {
                            pageID: 'hubspot Issues List Webapp',
                            pageName: document.title,
                            pagePath: document.location.pathname,
                            destinationURL: document.location.origin + document.location.pathname,
                            language: locale,
                            categories: [],
                            tags: []
                        },
                        attributes: {
                            hubspotInstance: hubspotData.content.hubspotInstance,
                            hubspotProjectName: hubspotData.content.hubspotProjectName,
                            hubspotPath: hubspotData.core.path
                        },
                        consentTypes: []
                    },
                    events: [],
                    // LoadCallbacks:[{
                    //     priority:5,
                    //     name:'Unomi tracker context loaded',
                    //     execute: () => {
                    //         window.cxs = window.wem.getLoadedContext();
                    //     }
                    // }],
                    wemInitConfig: {
                        contextServerUrl,
                        timeoutInMilliseconds: '1500',
                        // ContextServerCookieName: "context-profile-id",
                        activateWem: true,
                        // TrackerProfileIdCookieName: "wem-profile-id",
                        trackerSessionIdCookieName: 'wem-session-id'
                    }

                };
            }

            window.wem = syncTracker();
        }

        root.render(
            <React.StrictMode>
                <JahiaCtxProvider value={{
                    workspace,
                    locale,
                    host,
                    scope,
                    isEdit,
                    contextServerUrl,
                    cndTypes,
                    previewCm,
                    previewTarget,
                    isPreview
                }}
                >
                    <CxsCtxProvider>
                        <ApolloProvider client={client}>
                            <ConfigProvider hubspotData={hubspotData}>
                                <AppCtxProvider value={{
                                    ...appContext,
                                    core: hubspotData.core,
                                    content: hubspotData.content,
                                    config: {
                                        ...hubspotData.config
                                    }
                                }}
                                >
                                    <PrimeReactProvider>
                                        <App/>
                                    </PrimeReactProvider>
                                </AppCtxProvider>
                            </ConfigProvider>
                        </ApolloProvider>
                    </CxsCtxProvider>
                </JahiaCtxProvider>
            </React.StrictMode>
        );
    } catch (e) {
        console.error('Rendering error:', e);
        root.render(<ErrorHandler item={e.message} errors={e.errors}/>);
    }
};

window.hubspotUIApp = render;
serviceWorker.unregister();
