import {gql} from '@apollo/client';
import {CORE_NODE_FIELDS} from './fragments';

export const GetHubspot = gql`
    ${CORE_NODE_FIELDS}
    query getHubspot($workspace: Workspace!, $id: String!, $language: String!) {
        response: jcr(workspace: $workspace) {
            workspace
            hubspot: nodeById(uuid: $id) {
                ...CoreNodeFields
                title: displayName(language:$language)
            }
        }
    }`;

