import React, {useContext, useState} from 'react';

import {InputText} from 'primereact/inputtext';
import {InputTextarea} from 'primereact/inputtextarea';
import {Dropdown} from 'primereact/dropdown';
import {Button} from 'primereact/button';
import {useTranslation} from 'react-i18next';
import HubspotService from './hubspotService';
import marketFormStyles from './MarketFormStyles'; // Import styles
import {JahiaCtx, AppCtx} from '../../contexts';

const LeadGenForm = () => {
    const {t} = useTranslation();
    const [formData, setFormData] = useState({
        firstName: '',
        lastName: '',
        email: '',
        phone: '',
        company: '',
        jobTitle: '',
        industry: null,
        country: '',
        message: ''
    });

    const [validationErrors, setValidationErrors] = useState({});
    const [loading, setLoading] = useState(false);
    const [success, setSuccess] = useState(false);
    const [error, setError] = useState(null);

    const {host, scope} = useContext(JahiaCtx);
    const {content} = useContext(AppCtx);
    const actionUrl = host + '/sites/' + scope + '/home.hubspotAction.do';
    const formId = 'form-HubspotLead';

    const industryOptions = [
        {value: 'accounting', label: t('industries.accounting')},
        {value: 'advertising', label: t('industries.advertising')},
        {value: 'aerospace', label: t('industries.aerospace')},
        {value: 'agriculture', label: t('industries.agriculture')},
        {value: 'automotive', label: t('industries.automotive')},
        {value: 'banking', label: t('industries.banking')},
        {value: 'biotechnology', label: t('industries.biotechnology')},
        {value: 'construction', label: t('industries.construction')},
        {value: 'consumer_goods', label: t('industries.consumerGoods')},
        {value: 'education', label: t('industries.education')},
        {value: 'electronics', label: t('industries.electronics')},
        {value: 'energy', label: t('industries.energy')},
        {value: 'financial_services', label: t('industries.financialServices')},
        {value: 'government', label: t('industries.government')},
        {value: 'healthcare', label: t('industries.healthcare')},
        {value: 'hospitality', label: t('industries.hospitality')},
        {value: 'information_technology', label: t('industries.informationTechnology')},
        {value: 'insurance', label: t('industries.insurance')},
        {value: 'legal', label: t('industries.legal')},
        {value: 'manufacturing', label: t('industries.manufacturing')},
        {value: 'media', label: t('industries.media')},
        {value: 'mining', label: t('industries.mining')},
        {value: 'non_profit', label: t('industries.nonProfit')},
        {value: 'pharmaceuticals', label: t('industries.pharmaceuticals')},
        {value: 'real_estate', label: t('industries.realEstate')},
        {value: 'recruitment', label: t('industries.recruitment')},
        {value: 'retail', label: t('industries.retail')},
        {value: 'telecommunications', label: t('industries.telecommunications')},
        {value: 'transportation', label: t('industries.transportation')},
        {value: 'utilities', label: t('industries.utilities')},
        {value: 'other', label: t('industries.other')}
    ];

    const countries = [
        {name: 'United States', code: 'US'},
        {name: 'China', code: 'CN'},
        {name: 'Germany', code: 'DE'},
        {name: 'Japan', code: 'JP'},
        {name: 'India', code: 'IN'},
        {name: 'United Kingdom', code: 'GB'},
        {name: 'France', code: 'FR'},
        {name: 'Italy', code: 'IT'},
        {name: 'Brazil', code: 'BR'},
        {name: 'Canada', code: 'CA'},
        {name: 'Russia', code: 'RU'},
        {name: 'South Korea', code: 'KR'},
        {name: 'Mexico', code: 'MX'},
        {name: 'Australia', code: 'AU'},
        {name: 'Spain', code: 'ES'},
        {name: 'Indonesia', code: 'ID'},
        {name: 'Turkey', code: 'TR'},
        {name: 'Netherlands', code: 'NL'},
        {name: 'Saudi Arabia', code: 'SA'},
        {name: 'Switzerland', code: 'CH'},
        {name: 'Argentina', code: 'AR'},
        {name: 'Sweden', code: 'SE'},
        {name: 'Belgium', code: 'BE'},
        {name: 'Thailand', code: 'TH'},
        {name: 'Austria', code: 'AT'},
        {name: 'Nigeria', code: 'NG'},
        {name: 'Poland', code: 'PL'},
        {name: 'Norway', code: 'NO'},
        {name: 'South Africa', code: 'ZA'},
        {name: 'Malaysia', code: 'MY'},
        {name: 'Philippines', code: 'PH'},
        {name: 'Denmark', code: 'DK'},
        {name: 'Israel', code: 'IL'},
        {name: 'Singapore', code: 'SG'},
        {name: 'Vietnam', code: 'VN'},
        {name: 'Chile', code: 'CL'},
        {name: 'Finland', code: 'FI'},
        {name: 'Czech Republic', code: 'CZ'},
        {name: 'Colombia', code: 'CO'},
        {name: 'Pakistan', code: 'PK'},
        {name: 'Ireland', code: 'IE'},
        {name: 'Greece', code: 'GR'},
        {name: 'Portugal', code: 'PT'},
        {name: 'New Zealand', code: 'NZ'},
        {name: 'Hungary', code: 'HU'},
        {name: 'Romania', code: 'RO'},
        {name: 'Peru', code: 'PE'},
        {name: 'Hong Kong', code: 'HK'},
        {name: 'Bangladesh', code: 'BD'},
        {name: 'Egypt', code: 'EG'}
    ];

    const selectedCountryTemplate = (option, props) => {
        if (option) {
            return (
                <div className="flex align-items-center">
                    <img
                        alt={option.name}
                        src={`https://flagcdn.com/w40/${option.code.toLowerCase()}.png`}
                        className="mr-2"
                        style={{width: '18px'}}
                    />
                    <div>{option.name}</div>
                </div>
            );
        }

        // eslint-disable-next-line react/prop-types
        return <span>{props.placeholder}</span>;
    };

    const countryOptionTemplate = option => {
        return (
            <div className="flex align-items-center">
                <img
                    alt={option.name}
                    src={`https://flagcdn.com/w40/${option.code.toLowerCase()}.png`}
                    className="mr-2"
                    style={{width: '18px'}}
                />
                <div>{option.name}</div>
            </div>
        );
    };

    const handleChange = e => {
        const {name, value} = e.target;
        setFormData({...formData, [name]: value});
        setValidationErrors({...validationErrors, [name]: ''});
    };

    const validateForm = () => {
        const errors = {};

        if (!formData.firstName) {
            errors.firstName = t('validation.firstName');
        }

        if (!formData.lastName) {
            errors.lastName = t('validation.lastName');
        }

        if (!formData.email) {
            errors.email = t('validation.email.required');
        } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
            errors.email = t('validation.email.invalid');
        }

        if (!formData.phone) {
            errors.phone = t('validation.phone');
        }

        if (!formData.company) {
            errors.company = t('validation.company');
        }

        setValidationErrors(errors);
        return Object.keys(errors).length === 0;
    };

    const handleSubmit = async event => {
        event.preventDefault(); // Prevent form reload

        if (!validateForm()) {
            return;
        }

        setLoading(true);
        setSuccess(false);
        setError(null);

        const payload = {
            firstname: formData.firstName,
            lastname: formData.lastName,
            email: formData.email,
            phone: formData.phone,
            company: formData.company,
            jobtitle: formData.jobTitle,
            industry: formData.industry,
            country: formData.country?.name,
            message: formData.message
        };

        try {
            const response = await HubspotService.createLead(payload, actionUrl);
            if (response.status >= 200 && response.status < 300) {
                setSuccess(true);
            } else {
                const errorData = await response.json();
                setError(errorData.message || t('error.generic'));
            }
            // eslint-disable-next-line no-unused-vars
        } catch (err) {
            setError(t('error.network'));
        } finally {
            setLoading(false);
        }
    };

    return (
        <div style={marketFormStyles.container}>
            <form id={formId} name={formId} style={marketFormStyles.form} onSubmit={handleSubmit}>
                <h2>{content.title ? content.title : t('form.title')}</h2>
                <div style={marketFormStyles.grid}>

                    <div style={marketFormStyles.field}>
                        <label htmlFor="firstName" style={marketFormStyles.label}>
                            {t('form.firstName')}
                        </label>
                        <InputText
                            id="firstName"
                            name="firstName"
                            value={formData.firstName}
                            className={validationErrors.firstName ? 'p-invalid' : ''}
                            onChange={handleChange}
                        />
                        {validationErrors.firstName && (
                            <small style={{color: 'red'}}>{validationErrors.firstName}</small>
                        )}
                    </div>
                    <div style={marketFormStyles.field}>
                        <label htmlFor="lastName" style={marketFormStyles.label}>
                            {t('form.lastName')}
                        </label>
                        <InputText
                            id="lastName"
                            name="lastName"
                            value={formData.lastName}
                            className={validationErrors.lastName ? 'p-invalid' : ''}
                            onChange={handleChange}
                        />
                        {validationErrors.lastName && (
                            <small style={{color: 'red'}}>{validationErrors.lastName}</small>
                        )}
                    </div>
                    <div style={marketFormStyles.field}>
                        <label htmlFor="email" style={marketFormStyles.label}>
                            {t('form.email')}
                        </label>
                        <InputText
                            id="email"
                            name="email"
                            value={formData.email}
                            className={validationErrors.email ? 'p-invalid' : ''}
                            onChange={handleChange}
                        />
                        {validationErrors.email && (
                            <small style={{color: 'red'}}>{validationErrors.email}</small>
                        )}
                    </div>
                    <div style={marketFormStyles.field}>
                        <label htmlFor="phone" style={marketFormStyles.label}>
                            {t('form.phone')}
                        </label>
                        <InputText
                            id="phone"
                            name="phone"
                            value={formData.phone}
                            className={validationErrors.phone ? 'p-invalid' : ''}
                            onChange={handleChange}
                        />
                        {validationErrors.phone && (
                            <small style={{color: 'red'}}>{validationErrors.phone}</small>
                        )}
                    </div>
                    <div style={marketFormStyles.field}>
                        <label htmlFor="company" style={marketFormStyles.label}>
                            {t('form.company')}
                        </label>
                        <InputText
                            id="company"
                            name="company"
                            value={formData.company}
                            className={validationErrors.company ? 'p-invalid' : ''}
                            onChange={handleChange}
                        />
                        {validationErrors.company && (
                            <small style={{color: 'red'}}>{validationErrors.company}</small>
                        )}
                    </div>
                    <div style={marketFormStyles.field}>
                        <label htmlFor="jobTitle" style={marketFormStyles.label}>
                            {t('form.jobTitle')}
                        </label>
                        <InputText
                            id="jobTitle"
                            name="jobTitle"
                            value={formData.jobTitle}
                            onChange={handleChange}
                        />
                    </div>
                    <div style={marketFormStyles.field}>
                        <label htmlFor="industry" style={marketFormStyles.label}>
                            {t('form.industry')}
                        </label>
                        <Dropdown
                            id="industry"
                            name="industry"
                            value={formData.industry}
                            options={industryOptions}
                            onChange={e =>
                                setFormData({...formData, industry: e.value})}
                        />
                    </div>
                    <div style={marketFormStyles.field}>
                        <label htmlFor="country" style={marketFormStyles.label}>
                            {t('form.country')}
                        </label>
                        <Dropdown
                            filter
                            id="country"
                            name="country"
                            value={formData.country}
                            options={countries}
                            optionLabel="name"
                            placeholder={t('form.selectCountry')}
                            valueTemplate={selectedCountryTemplate}
                            itemTemplate={countryOptionTemplate}
                            onChange={e =>
                                setFormData({...formData, country: e.value})}
                        />
                    </div>
                </div>
                <div style={marketFormStyles.fullGrid}>

                    <div style={marketFormStyles.field}>
                        <label htmlFor="message" style={marketFormStyles.label}>
                            {t('form.message')}
                        </label>
                        <InputTextarea
                            id="message"
                            name="message"
                            value={formData.message}
                            style={{
                                width: '100%',
                                minHeight: '100px',
                                padding: '0.5rem',
                                border: '1px solid #ccc',
                                borderRadius: '4px',
                                fontSize: '1rem'
                            }}
                            onChange={handleChange}
                        />
                    </div>
                </div>
                <div style={marketFormStyles.submit}>
                    <Button
                            label={loading ? t('form.submitting') : t('form.submit')}
                            icon={loading ? 'pi pi-spin pi-spinner' : 'pi pi-check'}
                            disabled={loading}
                            type="submit" // Ensure button type is submit
                        />
                </div>
                {success && (
                <div style={{color: 'green'}}>
                    {t('form.success')}
                </div>
                    )}
                {error && (
                <div style={{color: 'red'}}>
                    {t(error)}
                </div>
                    )}
            </form>
        </div>
    );
};

export default LeadGenForm;
